@import url(https://fonts.googleapis.com/css?family=Lato);
#app-footer{width:100%;padding:0.75rem 0;color:#DFDFDF;text-align:center;background-color:transparent}#app-footer h4{margin:0.25rem;cursor:default}#div-logo-wrapper{display:flex;justify-content:center}.fa-logo{margin:8px}#svg-sjb{margin:4px}#svg-sjb circle,#svg-sjb polygon{fill:#DFDFDF;-webkit-transition:fill 0.5s;transition:fill 0.5s}#svg-sjb:hover circle,#svg-sjb:hover polygon{fill:goldenrod}

.page-home{flex:1 0 auto;display:flex;flex-direction:column;align-items:flex-start;justify-content:flex-start;background-size:cover;background-position:center;background-repeat:no-repeat;background-image:url(/extrasolar/static/media/apollo.85338426.jpg);text-shadow:1px 1px 24px #BBB;color:#DDD}.page-home header{margin:16px}.page-home h1{font-size:6.5vw;cursor:default}.page-home h2{font-size:3.33vw;cursor:default}.page-home nav{flex:1 0 auto;display:flex;flex-direction:column;align-items:flex-end;justify-content:center;width:100%}.page-home ul{width:100%;padding:0 2rem;list-style:none}.page-home ul li{margin:1.5vw 0;text-align:right;font-weight:bold}.page-home a:link,.page-home a:visited{color:#909090;text-decoration:none;-webkit-transition:color 0.5s;transition:color 0.5s}.page-home a:hover,.page-home a:active{color:#E0E0E0}.page-home footer h4{font-size:medium;font-size:initial}@media screen and (max-width: 480px){.page-home h1{font-size:36px}.page-home h2{font-size:20px}}@media screen and (min-width: 960px){.page-home{padding:1rem 0}.page-home h1{font-size:60px}.page-home h2{font-size:30px}}

.page-data{flex:1 0 auto;color:#DFDFDF;padding:96px 0;display:flex;flex-direction:column;justify-content:center;background-size:cover;background-position:center;background-repeat:no-repeat}#div-content-wrapper{color:#F0F0F0;width:75%;padding:24px;background:#1F1F1F;border:4px solid #7F7F7F;border-radius:8px;max-width:720px;text-align:justify;opacity:0.9}#main-data p{margin:16px 0}#section-live-data{display:flex;flex-direction:row;justify-content:center;align-items:center}#section-live-data p{max-width:288px;margin:16px auto}.section-chart-container{margin:16px auto}#section-chart-categorical{display:flex;flex-wrap:wrap;flex-direction:row;justify-content:center;align-items:center}#div-chart-categorical{width:320px;height:320px;display:inline-block;overflow:hidden}#div-chart-categorical svg{max-width:320px;margin:16px auto;display:inline-block}table{margin:auto;width:288px;text-align:center;border:2px #333333 solid}caption{padding:12px;text-align:center;background:#777777;border:2px #333333 solid}th{padding:8px;background:#999999}tbody tr td{padding:2px}tfoot tr th{padding:8px;text-align:center;background:#777777}

#main-solarsystems{display:flex;flex-direction:column;flex:1 0 auto;color:#DFDFDF}#div-filters{display:flex;flex-wrap:wrap;background:#7F7F7F;padding:24px}#div-filter-stars{display:flex;flex-direction:column;min-width:288px;margin:0 auto}#div-filter-planets{display:flex;flex-direction:column;min-width:288px;margin:0 auto}.slider-label{margin:24px 0}#section-container{padding-top:4px;height:160px;overflow:scroll}.div-virtual-list{list-style:none;background-color:#3f51b5}.div-virtual-item{width:90%;margin:auto;background-color:darkgreen}

#main-about{flex:1 0 auto;display:flex;flex-direction:column;justify-content:center;align-items:center;background-size:cover;background-position:center;background-repeat:no-repeat;background-image:url(/extrasolar/static/media/alpha-centauri-h.b2eed314.jpg);text-shadow:1px 1px 32px #7F7F7F;padding:12px 0}#div-content-wrapper{color:#0F0F0F;width:75%;padding:24px;background:#BFBFBF;border:4px solid #7F7F7F;border-radius:8px;max-width:720px;text-align:justify;opacity:0.9}#main-about p{margin:16px 0}#main-about a:link,#main-about a:visited{color:#2F2F2F;-webkit-transition:color 0.5s;transition:color 0.5s}#main-about a:hover,#main-about a:active{color:#6F6F6F}

#page-404{flex:1 0 auto;color:red;display:flex;flex-direction:column;justify-content:center;align-items:center;background-size:cover;background-position:center;background-repeat:no-repeat;background-image:url(/extrasolar/static/media/black-hole.5e39783f.jpg)}

button{width:48px;height:48px;margin:12px;outline:none;border-radius:50%}

#app-header{width:100%;color:#F0F0F0;background-color:#2F2F2F;text-shadow:2px 2px 8px #0F0F0F;align-items:center;display:grid;grid-template-columns:auto 1fr auto}#app-logo{height:40px;width:40px;-webkit-animation:app-logo-spin infinite 20s linear;animation:app-logo-spin infinite 20s linear}#divAppTaglines{flex:1 0 100%;text-align:left}#button-audio{padding:4px;background-color:#0F0F0F}@-webkit-keyframes app-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes app-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@media screen and (max-width: 600px){#app-header{flex-wrap:wrap}#app-header h1{font-size:1rem;text-align:center}#app-header h4{font-size:0.75em;text-align:center}}

.app{display:flex;min-height:100vh;flex-direction:column;text-align:center}.page{flex:1 0 auto}

#app-nav{padding:8px;background-color:#4F4F4F;text-shadow:2px 2px 8px #0F0F0F}#app-nav ul{display:flex;justify-content:center;list-style:none}#app-nav li{margin:2px;height:32px;background:#4F4F4F;text-align:center;display:table}#app-nav a{margin:auto;width:144px;font-size:16px;color:#F0F0F0;text-decoration:none;display:table-cell;vertical-align:middle}#app-nav a:hover,#app-nav a:active{color:#F0F0F0;background-color:#4F4F4F;text-decoration:none}#app-nav a:link,#app-nav a:visited{height:20px;color:#F0F0F0}@media screen and (max-width: 480px){#app-nav li{height:32px}#app-nav a{width:72px;font-size:10px}}

*{margin:0;padding:0}body{background:#1F1F1F;font-family:Lato, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif}#react-root{display:flex;min-height:100vh;flex-direction:column}main{flex:1 0 auto;display:flex;flex-direction:column}a:link,a:visited{color:#F0F0F0;text-decoration:none;-webkit-transition:color 0.75s;transition:color 0.75s}a:hover,a:active{color:#000000}

